.widget {

    width: 100%;
    margin: 0 1em 1em;
    padding-top: 1em;
    margin: 0 0 1em;
    padding: 0 0 1em;
    font-family: $titillium_web;

    ul,
    ol {
        padding: .7em 0 0;
        margin-bottom: 0;
        //list-style-type: none;
        padding-top: 0;
        margin-top: 0;

            li {
                display: block;
                line-height: 2em;

                ul,
                ol {

                    margin-left: .5em;

                    @media screen and (min-width: 30em) {
                            margin-left: 1em;
                    }

                }

            }
    }

    // a {
    //
    //     text-decoration: none;
    //
    //     &:hover,
    //     &:focus {
    //             /*border-bottom: $color__link-hover;*/
    //     }
    // }

    /* Make sure select elements fit in widgets. */
    select {
        width: 100%;
        height: 2.5em;
        padding: 10px;
        //font-family: $titillium_web;
        border: 1px solid #ccc;
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    .widget-title {
        margin-top: 0;
        margin-bottom: .5em;
        @extend .h4;
        font-weight: bold;
        color: $color-light;
    }

}

/**
 * Show list items on 'Recent Posts' and other specific list-type footer widgets
 */
.widget.widget_recent_entries {

		ul > li {

			display: list-item;
			list-style: disc inside;
			line-height: 1.5;
			margin-bottom: 0.35em;

		}

}

