/* Search widget */
.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid #c3c3c3;
	border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;

	caption {
		padding: .5em 0;
		font-size: 1em;
		font-weight: 500;
		text-align: left;
	}

	thead {
		background: transparent;
		color: black;

		th {
			border-bottom-width: 2px;
		}
	}

	td {
		padding: .2em;
		font-size: .8em;
		text-align: center;
		background: white;
		border: 1px solid hsl(0, 0%, 80%);
	}

	th {
		border: none;
		text-align: center;
	}

	.pad {
		background: #eaeaea;
	}

	a {
		font-weight: 900;
		text-decoration: none;
	}

}

/* MailChimp signup form */
.widget #mc_embed_signup {
    p {
        margin: 0.5em 0 1em;
    }
    .indicates-required {
        display: none;
    }
    .mc-field-group {
        clear:left;
        position:relative;
        width:100%;
        padding-bottom:3%;
        min-height:50px;
        .asterisk {
            color: red;
        }

    }
    .mc-field-group label {
        display: block;
        font-weight: bold;
        font-size: 85%;
        margin-bottom: 0.25em;
    }
    .mc-field-group input {
        display: block;
        width: 100%;
        padding: 8px 0;
        text-indent: 2%;

    }
    #mce-responses .response {
        margin-bottom: .75em;
    }

    @media #{$desktop__breakpoint} {
        .mc-field-group input,
        div.mce_inline_error {
            width: 96%;
        }

    }

}

